
import { ref, defineComponent, computed, onMounted, watch } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import { getAuth, signOut } from "firebase/auth";
import { useUserStore } from "@/stores/user";
import { useTodoStore } from "@/stores/todo";
import { TodoModel } from "@/models/todo.model";

export default defineComponent({
  name: "HomePage",

  setup() {
    const auth = getAuth();
    const user = useUserStore();
    const router = useRouter();

    // Todo
    const todoStore = useTodoStore();
    const newTodoTitle = ref("");
    const searchTerm = ref("");

    const options = ["–í—Å–µ", "–°–µ–≥–æ–¥–Ω—è—à–Ω–∏–µ"];
    const selectedOption = ref<string>("");

    onMounted(async () => {
      const savedFilters = sessionStorage.getItem("filteredProducts");
      const saveSelectedOption = sessionStorage.getItem("selectedOption");

      if (savedFilters && saveSelectedOption !== "–í—Å–µ") {
        todoStore.todos.splice(0, todoStore.todos.length);

        for (const obj in JSON.parse(savedFilters)) {
          todoStore.todos.push(JSON.parse(savedFilters)[obj]);
        }
      } else {
        // –ü–æ–ª—É—á–∞–µ–º –∑–∞–º–µ—Ç–∫–∏ –∏–∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å–µ—Ä–≤–µ—Ä–∞
        getAllTodos();
      }
    });

    watch(selectedOption, (newValue) => {
      sessionStorage.setItem("selectedOption", selectedOption.value);

      switch (newValue) {
        case "–í—Å–µ":
          // –§–∏–ª—å—Ç—Ä –¥–ª—è –≤—Å–µ—Ö –∑–∞–º–µ—Ç–æ–∫
          getAllTodos();
          break;
        case "–°–µ–≥–æ–¥–Ω—è—à–Ω–∏–µ":
          // –§–∏–ª—å—Ç—Ä –¥–ª—è —Å–µ–≥–æ–¥–Ω—è—à–Ω—ã—Ö –∑–∞–º–µ—Ç–æ–∫
          todoStore.filterTodayTodos();
          break;
        default:
          getAllTodos();
          break;
      }
    });

    // –î–æ–±–∞–≤–ª–µ–Ω–∏–µ –∑–∞–¥–∞—á–∏
    const addTodo = () => {
      if (newTodoTitle.value.trim() !== "") {
        todoStore.add(newTodoTitle.value);
        newTodoTitle.value = "";
      }
    };

    const unCheck = () => (selectedOption.value = "");

    // –ó–∞–¥–∞—á–∞ –∑–∞–≤–µ—Ä—à–µ–Ω–∞
    const toggleCompleted = (id: number) => todoStore.toggleCompleted(id);

    // –ü–æ–ª—É—á–∞–µ–º –∑–∞–º–µ—Ç–∫–∏
    const getAllTodos = async () => {
      try {
        const res = await axios.get<TodoModel[]>("http://localhost:4000/todos");
        todoStore.todos = res.data;
      } catch (error) {
        console.error(error);
      }
    };

    // –£–¥–∞–ª–µ–Ω–∏–µ –∑–∞–º–µ—Ç–∫–∏
    const removeTodo = (id: number) => todoStore.remove(id);

    // –î–æ–±–æ–≤–ª—è–µ—Ç —Å—Ç–∞—Ç–∏—Å—Ç–∏—á–µ—Å–∫—É—é –∑–∞–º–µ—Ç–∫—É —Å–æ –≤—á–µ—Ä–∞—à–Ω–µ–π –¥–∞—Ç–æ–π
    const createTomorrowTodo = () => {
      todoStore.filterTomorrowObjects();
      selectedOption.value = "";
    };

    // –í—ã—Ö–æ–¥ –∏–∑ –∞–∫–∫–∞—É–Ω—Ç–∞
    function handleSignOut() {
      signOut(auth)
        .then(() => {
          // Sign-out successful.
          sessionStorage.removeItem("user");
          router.push("/login");
        })
        .catch((error) => {
          // An error happened.
          console.log(error);
        });
    }

    // computed –¥–ª—è –ø–æ–∏—Å–∫–∞
    const filteredTodos = computed(() =>
      todoStore.todos.filter((todo: TodoModel) =>
        todo.title.toLowerCase().includes(searchTerm.value.toLowerCase())
      )
    );

    // –î–æ–±–∞–≤–ª—è–µ–º –≤—ã–±—Ä–∞–Ω–Ω—ã–µ —Ñ–∏–ª—å—Ç—Ä—ã –≤ sessionStorage –¥–ª—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –ø–æ—Å–ª–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    watch(searchTerm, async () => {
      sessionStorage.setItem(
        "filteredProducts",
        JSON.stringify({ ...filteredTodos.value })
      );

      const savedFilters = sessionStorage.getItem("filteredProducts");

      if (savedFilters) {
        todoStore.todos.splice(0, todoStore.todos.length);

        for (const obj in JSON.parse(savedFilters)) {
          todoStore.todos.push(JSON.parse(savedFilters)[obj]);
        }
      }
    });

    return {
      user,
      options,
      searchTerm,
      newTodoTitle,
      filteredTodos,
      selectedOption,
      unCheck,
      addTodo,
      removeTodo,
      getAllTodos,
      handleSignOut,
      toggleCompleted,
      createTomorrowTodo,
    };
  },
});
