import { createRouter, createWebHistory } from 'vue-router';
import Home from '@/views/home/HomePage.vue';
import Login from '@/views/login/LoginPage.vue';

const routes = [
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      isAuth: false
    }
  },
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      isAuth: true
    }
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach((to, from, next) => {
  const isAuthRoute = to.matched.some(record => record.meta.isAuth);

  // –ï—Å–ª–∏ –Ω–µ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω —Ä–µ–¥–∏—Ä–µ–∫—Ç–∏–º –≤ login
  if (isAuthRoute && !isUserSignedIn()) {
    next('/login');
  } else {
    next();
  }
});

// –ü—Ä–æ–≤–µ—Ä—è–µ–º –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–Ω –ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∏–ª–∏ –Ω–µ—Ç
function isUserSignedIn() {
  const user = localStorage.getItem("user");
  if (user !== null) {
    const { isSignedIn } = JSON.parse(user);
    return isSignedIn;
  }
  return false;
}

export default router;